import Authorization from "../user/auth.service";

const TextService = {          
    async getText(key) { //! Obtiene los datos x un ID
        try {
            const { status, data } = await Authorization.checkLogin(`/text/by-key/${key}`)
            if (status == 200) {
                return data
            }
        } catch (e) {
            console.log(e.response)
        }
    }    
}

export default TextService