import Vue from "vue";
import VueRouter from "vue-router";
import auth from "@/services/user/auth.service"

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: () =>
      import(/* webpackChunkName: "about" */ "@/views/Home.vue"),
  },
  {
    path: "/acerca-de",
    name: "About",
    component: () =>
      import(/* webpackChunkName: "about" */ "@/views/About.vue"),
  },
  // POLES ROUTES
  {
    path: "/destinos/:keyword",
    name: "SinglePole",
    component: () => import("@/views/Pole.vue"),
  },
  {
    path: "/destinos/:poleKey/:productKey",
    name: "Products",
    component: () =>
      import("@/views/Product.vue"),
  },

  // PRODUCTS
  {
    path: "/destinos/:poleKey/:productKey/:keyword",
    name: "HotelDetail",
    component: () =>
      import("@/views/Products/HotelDetail.vue"),
  },
  
  // SEARCH-RESULT
  {
    path: "/resultado-de-busqueda",
    name: "SearchResult",
    component: () =>
      import("@/views/SearchResult.vue"),
  },

  // TERM & POLICY
  {
    path: "/terminos-politicas",
    name: "TermsAndPolicy",
    component: () => import('@/views/Policy.vue')
  },

  // CONTACT US
  {
    path: "/contactenos",
    name: "Contact",
    component: () =>
      import("@/views/Contacto.vue"),
  },

  // SEND REQUEST
  {
    path: "/envio-de-solicitud",
    name: "SendRequest",
    component: () =>
      import("@/views/Request.vue"),
  },

  // CLIENT SUBSCRIPTION
  {
    path: "/usuario/suscripcion",
    name: "NewClient",    
    component: () =>
      import("@/views/Clients/NewClient.vue"),      
  },
  {
    path: "/usuario/olvide-mi-clave",
    name: "ForgotPassword",
    component: () =>
      import("@/views/Clients/ForgotPassword.vue"),
  },  
  {
    path: "/usuario/olvide-mi-clave/:token",
    name: "ResetPassword",
    component: () =>
      import("@/views/Clients/ForgotPassword.vue"),
  }, 
  {
    path: "/verificacion-de-cuenta/:token",
    name: "VerifyUser",
    component: () =>
      import("@/views/Clients/VerifyUser.vue"),
  },
  
  // Admin
  {
    path: "/admin",
    name: "Admin",        
    component: () => 
      import("@/views/Admin/Admin.vue")
  },
  {
    path: "/admin/:CrudKey",
    name: "CrudKey",
    component: () =>
      import("@/views/Admin/Admin.vue"),    
  },  
  {
    path: "/404",
    name: "NotFound",
    component: () =>
      import("@/views/404.vue"),    
  },  

  // 404
  {
    // path: '/:catchAll()',
    path: '*',
    redirect: '/404'
  }
  
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
