<template>
  <footer class="mt-auto pt-10 pb-4 px-4 z-0">          
    <div class="flex flex-col md:flex-row text-white gap-6">
      <figure class="md:w-3/12 order-1 md:order-1 flex flex-col items-center justify-evenly gap-5 py-3 sm:py-0">
          <img
          :src="logo_img"
          class="w-2/5 m-auto sm:m-0"
          alt="logo"
          />
        <ul class="social-media flex flex-row justify-center text-3xl sm:justify-end gap-6 p-2">
          <li class="px-3 pt-1"><a href="#"><i class="fab fa-instagram"></i></a></li>
          <li class="px-4 pt-1"><a href="#"><i class="fab fa-facebook-f"></i></a></li>
          <li class="px-3 pt-1"><a href="#"><i class="fab fa-whatsapp"></i></a></li>
        </ul>
      </figure>
      <div class="md:w-2/12 order-3 md:order-1 flex flex-col gap-4 md:text-left md:border-l-2 md:px-6">
        <h3 class="uppercase text-2xl font-DIN-Bold md:block hidden">soporte</h3>
        <ul class="text-xl">
          <li><router-link :to="{name: 'About'}">Quienes Somos</router-link></li>
          <li><router-link :to="{name: 'TermsAndPolicy'}">Términos y políticas</router-link></li>
          <li><router-link :to="{name: 'Contact'}">Contáctanos</router-link></li>
        </ul>
      </div>
      <div class="order-4 md:order-1 flex flex-col gap-4 md:text-left md:border-l-2 md:px-6">
        <h3 class="uppercase text-2xl font-DIN-Bold md:block hidden">Ubicacion</h3>
        <div class="text-xl">
          <a href="https://goo.gl/maps/P76yQdQ7y7BYijwv9">
            <pre>{{ dir }}</pre>
          </a>
        </div>
      </div>
      <div class="md:w-2/12 order-2 md:order-1 flex flex-col gap-4 md:text-left md:border-l-2 md:px-6">
        <h3 class="uppercase text-2xl font-DIN-Bold md:block hidden">contacto</h3>
        <div class="text-xl"><pre>{{ phones }}</pre></div>
      </div>
    </div>
    <div class="text-white py-6 mx-auto">
      <small>{{ copyright }}</small>
    </div>
  </footer>
</template>

<script>
import config from "@/config";
import textServices from "@/services/common/texts.service";

export default {
  name: "Footer",
  data() {
    return {
      logo_img: config.gallery_url + "general/logo/logo-white.png",
      dir: "",
      phones: "",
      copyright: ""
    };
  },
  created() {
    const texts = [];
    texts.push(textServices.getText("direccion"));
    texts.push(textServices.getText("contact-phone"));
    texts.push(textServices.getText("copyright"));

    Promise.all(texts).then(t => {
      // console.log(t)
      if (t) {
        this.dir = t[0] ? t[0].description : "";
        this.phones = t[1] ? t[1].description : "";
        this.copyright = t[2] ? t[2].description : "";
      }
    });
  }
};
</script>
<style scoped>
footer {
  background-color: var(--dark-blue);
  grid-template-columns: 15% auto 15%;
  grid-template-rows: auto auto auto;
  /* box-shadow: -10px -7px 40px #000; */
}
footer .social-media p {
  font-size: 14px;
}
footer .social-media li {
  background-color: white;
  border-radius: 100%;
  padding: 3px 10px;
  color: var(--blue);
}
@media screen and (min-width: 640px) {
  footer {
    grid-template-columns: 15% 20% 15% 35% 15%;
  }
  footer .social-media p {
    text-align: right;
  }
}
</style>
